.telegram {
  display: flex;
  justify-content: center;
  align-items: center;
}

.TelegramContainer {
  background-color: #0768f6;
  margin-top: 48px !important;
  width: 1050px;
  border-radius: 4px 4px 4px 0;
  padding-top: 42px;
}

.tele-members > img {
  /* /* width: 98px; */
  height: 122px;
  margin: 18px;
}
.tele-txt {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 49px;
  /* margin-top: 42px; */

  color: #ffffff;
}

.tele-btn {
  color: #0768f6;
  background: #fff;
  padding: 11px 16px;
  border: none;
  border-radius: 4px 4px 4px 0;
  margin-top: 22px;
  margin-bottom: 20px;
}
@media (max-width: 375px) {
  .tele-members > img {
    width: 60px;
    margin: 10px;
  }
  /* .tele-txt {
    margin-left: 30px;
    font-size: 18px;
    
  } */
}

@media (max-width: 420px) {
  .tele-members > img {
    width: 60px;
  }
  .tele-txt {
    /* width: 60%; */
    /* margin-left: 30px; */
