}

@media screen and (min-width: 420px) and (max-width: 960px) {
  .TelegramContainer {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .tele-members > img {
    margin-left: -34px !important;
    width: 170px !important;
  }

  .tele-div {
    margin-left: 15px;
  }

  .tele-txt {
    font-size: 15px;
    line-height: 35px;
  }
}

