.FooterContainer {
  background-position: right;
  background-size: 40vw;
  background-repeat: no-repeat;
  background-image: url(../Assets/Ellipse2.png),
    linear-gradient(0deg, rgba(255, 255, 255, 0.979), rgba(255, 255, 255, 0.959));
  height: 100%;
  width: auto;
  margin: 0;
  padding: 0;
}
@media (max-width: 768px) {
  .FooterContainer {
    background: none;
  }
}
