.testimony {
  background: #f0f6ff;
}
.testimony-center {
  padding: 38px 0px 30px 0px;
}
.testimony-img {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.angela {
  width: 70%;
  margin-top: 30px;
  margin-left: 100px;
}
.testimony-img-details {
  margin-top: 18px;
  margin-bottom: 18px;
}
.testimony-name {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #121212;
  margin-bottom: 2px;
}

.testimony-name-pos {
  /* font-family: "Inter"; */
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #666666;
  margin-top: 2px;
}
.header {
  font-weight: 600;
  font-size: 36px;
  line-height: 28px;
  /* identical to box height, or 78% */

  text-align: center;

  /* Body header color */

  color: #121212;
  margin-bottom: 40px;
}
.header > span {
  color: #0768f6;
  border: 2px solid #fd740e;
  border-radius: 50%;
}
.avatar {
  margin-top: 73px;
  margin-left: 170px;
}

.avatar > img {
  width: 50px;
  height: 50px;
  margin: 4px;
}
.testimonies {
  margin-left: 20px;
  margin-top: 32px;
  font-weight: 500;
  font-size: 18px;
  line-height: 40px;
  color: #666666;
  text-align: center;
}
.tag {
  margin-left: 30px;
  text-align: center;
}

.tag > span {
  color: red;
}
@media (max-width: 379px) {
  .tag {
    font-size: 12px;
    margin-right: 30px;
  }
}
@media (max-width: 420px) {
  .testimony-img {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .testimony-img > img {
    width: 70%;
    margin-left: 10px;
  }
  .testimony-img-details {
    margin-left: 10px;
  }
  .header {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    margin-left: 30px;
    margin-top: 50px;
    margin-bottom: -20px;
  }

  .avatar {
    display: flex;
    width: 25%;
    /* height: 45px; */
    margin-left: 70px;
    /* justify-self: center;
    justify-content: center;
    text-align: center; */
  }
  .avatar > img {
    width: 50%;
  }
  .testimonies {
    text-align: center !important;
    margin-right: 90px;
    width: 90%;
  }
}
@media screen and (min-width: 420px) and(max-width: 768px) {
  .testimony {
    width: 100%;
  }
  .testimony-center {
    padding: 0;
    margin: 0;
  }
  .testimony-img {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .testimony-img > img {
    width: 70%;
    padding-left: 20px;
  }
  .testimony-img-details {
    margin-left: 20px;
  }
  .avatar {
    text-align: center;
    margin-left: 40px;
  }
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .header {
    margin-top: 70px;
  }
  .avatar {
    padding-right: 100px;
    width: 100%;
  }
  .avatar > img {
    margin: 10px;
  }
}
