.footer-section {
  padding: 142px 102px 10px;
}

.footer-logo {
  align-items: center;
  display: flex;
  color: #0768f6;
}
.footer-logo > img {
  width: 30.56px;
}
.footer-logo > p {
  margin-top: 0.9rem;
  margin-left: 10px;
  /* font-weight: bold; */
  font-size: 25px;
}
.footer-about {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;

  display: flex;
  align-items: flex-end;
  letter-spacing: -0.02em;
  margin-top: 40.1px;
  width: 100%;
  /* body text color */

  color: #666666;
}
.Location-address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 37px;
}
.Location-address > img {
  margin-right: 18px;
}

.Location-address > p {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* or 137% */

  display: flex;
  /* align-items: flex-end; */

  /* body text color */

  color: #666666;
}
.Location-mail {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 29px;
}
.Location-mail > img {
  margin-right: 17px;
}
.Location-mail > p {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* identical to box height, or 137% */

  display: flex;
  align-items: flex-end;

  /* body text color */

  color: #666666;
}

.social-icon {
  margin-top: 32px;
}
.social-icon > img {
  margin-right: 34px;
}
.Company-section > ul {
  margin-top: 53px;
}
.Company-section > ul > li {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 16px;
  margin-top: 19px;
  list-style: none;
  margin-left: 50px;
  margin-top: 30px;
  /* identical to box height */

  /* Body header color */

  color: #666666;
}
.Company-section > p {
  font-family: Inter;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  margin-top: 10px;
  margin-left: 50px;
  /* identical to box height */

  /* Primary color */

  color: #0768f6;
}
/* .Company-section {
  margin-bottom: 90px;
  margin-left: 47px;
  margin-right: 51px;
} */

.contact-session {
  margin-left: 50px;
}
.footer-copy-right {
  padding: 0 !important;
}

@media (max-width: 379px) {
  .footer-section {
    padding: 142px 50px 10px 102px;
  }
}
@media (max-width: 420px) {
  .footer-section {
    /* width: 100%; */
    /* margin-left: -60px;*/
    margin-top: -90px;
  }
  .footer-about {
    width: 100%;
  }
  .Location-mail {
    margin-top: 5px;
  }
  .Location-mail > img {
    margin-top: -5px;
  }
  .Location-address {
    width: 100%;
  }
  .Location-address > img {
    margin-top: -50px;
  }
  .social-icon {
    width: 100%;
  }
  .social-icon > img {
    margin-right: 10px;
    padding-right: 15px;
    margin-top: -20px;
  }
  .Company-section {
    width: 100%;
    margin-top: 60px;
    margin-left: -60px;
  }
  .Company-section > ul {
    margin-top: 10px;
    margin-left: -25px;
  }
  .Company-section > ul > li {
    margin-top: 15px;
    margin-bottom: 30px;
  }
}

@media screen and (max-width: 960px) {
  .footer-section {
    margin-left: -60px;
  }

  .footer-about {
    margin-top: 15px;
  }
  .Company-section > ul {
    margin-top: 10px;
    margin-left: -25px;
  }
  .Company-section > ul > li {
    margin-top: 35px;
    margin-bottom: 30px;
  }
  /* .Company-section > p {
    width: 100%;
  } */
  .Location-address > img {
    margin-top: -50px;
  }
  .Location-mail > img {
    margin-top: -2px;
  }
  .social-icon > img {
    margin-right: 10px;
    padding-right: 15px;
    margin-top: -20px;
  }
}
.footer {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
  padding: 50px;
}

footer > * {
  flex: 1 100%;
}

.footer-left {
  margin-left: 1.25rem;
  margin-bottom: 2rem;
}
h2 {
  font-weight: 600;
  font-size: 17px;
  margin-top: 2rem;
}
footer ul {
  list-style: none !important;
  padding-left: 0;
}
.footer li {
  line-height: 2rem;
  list-style: none;
}
.box li {
  list-style: none;
  margin-top: 32px;
}
.footer-right {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-flow: row wrap;
}

.footer-right > * {
  flex: 1 50%;
  margin-right: 1.25%;
}
.footer-bottom {
  text-align: left;
  margin-top: 100px;
}

.footer-left p {
  padding-right: 20%;
}
.socials img {
  display: inline-block;
  margin-right: 12px;
}
.footerlist {
  margin-top: 15px;
  margin-bottom: 15px;
}
.footerlist > h2 {
  color: #0768f6;
}
@media screen and (min-width: 600px) {
  .footer-right > * {
    flex: 1;
  }
  .footer-left {
    flex: 1 0px;
  }
  .footer-right {
    flex: 2 0px;
  }
  .socials img {
    display: inline-block;
    margin-right: 30px;
  }
}
@media screen and (min-width: 600px) and (max-width: 900px) {
  .footer-right > * {
    flex: 1;
  }
  .footer-left {
    flex: 1 0px;
  }
  .footer-right {
    flex: 3 900px;
  }
  .socials img {
    display: inline-block;
    margin-right: 30px;
  }
  .box li {
    font-size: 20px;
  }
}
@media screen and (max-width: 600px) {
  .footer {
    padding: 15px;
  }
  .footer-p2 {
    display: none;
  }
  .box li {
    list-style: none;
    margin-top: 17px;
  }
}
