@media screen and (max-width: 960px) {
  .row {
    max-width: 90% !important;
  }
  body {
    overflow-x: hidden;
    max-width: 900px !important;
  }
}
@media screen and (max-width: 420px) {
  body {
    overflow-x: hidden;
    max-width: 420px !important;
  }
}
@media screen and (max-width: 375px) {
  body {
    overflow-x: hidden;
    max-width: 375px !important;
  }
}
